class OperateLights {
  constructor() {
    this.yellowButton = document.querySelector(".yellowButton");
    this.blueButton = document.querySelector(".blueButton");
    this.greenButton = document.querySelector(".greenButton");
    this.redButton = document.querySelector(".redButton");

    this.timing = 300;
    this.interval = [];
  }

  powerOnLights() {
    this.toggleLights(
      [this.yellowButton, this.blueButton, this.redButton, this.greenButton],
      0
    );

    let i = setTimeout(() => {
      this.toggleLights(
        [
          this.yellowButton,
          this.blueButton,
          this.redButton,
          this.greenButton,
          this.yellowButton,
          this.blueButton,
          this.redButton,
          this.greenButton,
          this.yellowButton,
          this.blueButton,
          this.redButton,
          this.greenButton,
        ],
        150
      );
    }, this.timing);
    this.interval.push(i);
  }
  powerOffLights() {
    this.interval.forEach((e) => {
      clearInterval(e);
    });
    this.toggleLights(
      [this.yellowButton, this.blueButton, this.redButton, this.greenButton],
      10,
      true
    );
  }
  toggleLights(arr, interval, forceOff) {
    let delay = 0;
    if (interval === 0) {
      //if interval is 0 default delay setTimeout for adding to 0 and removing to 200ms
      arr.forEach((e, i) => {
        let j = setTimeout(function () {
          e.classList.add(`button-${e.dataset.num}`);
        }, 0);
        let k = setTimeout(() => {
          e.classList.remove(`button-${e.dataset.num}`);
        }, this.timing);
        this.interval.push(j);
        this.interval.push(k);
      });
    } else if (forceOff === true) {
      arr.forEach((e) => {
        e.classList.remove(`button-${e.dataset.num}`);
      });
    } else {
      arr.forEach((e, i) => {
        let j = setTimeout(function () {
          e.classList.add(`button-${e.dataset.num}`);
        }, delay);
        delay += interval;
        let k = setTimeout(function () {
          e.classList.remove(`button-${e.dataset.num}`);
        }, delay);
        delay += interval;
        this.interval.push(j);
        this.interval.push(k);
      });
    }
  }
}

let LightOperator = new OperateLights();

class SimonSaid {
  constructor() {
    this.count = 0;
    this.on = false;
    this.strict = false;

    //elementNode
    this.displayCount = document.querySelector("#displayCount");
    this.powerButton = document.querySelector("#powerButton");

    //binding
    this.power = this.power.bind(this);

    this.interval = [];
  }

  power() {
    this.on = !this.on;
    if (this.on) {
      LightOperator.powerOnLights();
      this.powerButton.style.backgroundColor = "green";
      this.powerButton.textContent = "On";
      this.countDown();
    } else {
      this.interval.forEach((e) => {
        clearInterval(e);
      });
      LightOperator.powerOffLights();
      this.count = 0;
      this.powerButton.style.backgroundColor = "red";
      this.powerButton.textContent = "Off";
      this.updateCount(this.count);
    }
  }
  updateCount(count) {
    this.displayCount.innerHTML = count;
  }
  countDown() {
    let count = 3;
    let delay = 0;
    while (count >= 0) {
      let curCount = count;
      let i = setTimeout(() => {
        this.updateCount(curCount);
      }, delay);
      count--;
      delay += 1000;
      this.interval.push(i);
    }
  }
  hard() {
    console.log("so strict");
  }
  reset() {
    console.log("resetting");
  }
}

let Game = new SimonSaid();

let resetNode = document.querySelector(".resetButton");
let powerNode = document.querySelector(".powerButton");
let strictNode = document.querySelector(".strictButton");

if (resetNode) {
  resetNode.addEventListener("click", Game.reset);
  powerNode.addEventListener("click", Game.power);
  strictNode.addEventListener("click", Game.hard);
}
